import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const router = new VueRouter({
	mode: 'history',
	routes: [
		{
			path: '/',
			name: 'home',
			component: () => import('@/components/Homepage'),
			meta: { title: 'Home' },
		},
		{
			path: '/services',
			name: 'services',
			component: () => import('@/components/Services'),
			meta: { title: 'Services' },
		},
		{
			path: '/about-us',
			name: 'aboutus',
			component: () => import('@/components/AboutUs'),
			meta: { title: 'About Us' },
		},
		{
			path: '/contact-us',
			name: 'contactus',
			component: () => import('@/components/ContactUs'),
			meta: { title: 'Contact Us' },
		},
		{
			path: '/faq',
			name: 'faq',
			component: () => import('@/components/FAQ'),
			meta: { title: 'FAQ' },
		},

		// {
		// 	name: '404',
		// 	path: '/resource-not-found',
		// 	component: () => import('@/components/404'),
		// 	meta: { title: 'Page not found' },
		// 	props: true,
		// },
		// {
		// 	path: '*',
		// 	redirect: to => {
		// 		return {
		// 			name: '404',
		// 			params: { badUrl: to.fullPath },
		// 		}
		// 	},
		// 	component: () => import('@/components/404'),
		// },
	],
});

export default router;
